import apiError from "../../../components/apiError";
import fieldValidation from "../../../components/fieldValidation";
import postHeader from "../../../components/postHeader";
import fileUploadFun from "./fileUploadFun";

async function handleSubmitFun(event, url) {
    event.preventDefault();
    if (this.state.inwardList.length === 0) {
        let msg = "Please select Inward List before Save"
        return fieldValidation.call(this, msg, "inward_no")
    }
    else if (this.state.applicant_type === "") {
        let msg = "Please select Applicant before Save"
        return fieldValidation.call(this, msg, "applicant_type")
    }
    else if (this.state.sample_type === "") {
        let msg = "Please select sample type before Save"
        document.getElementById("sample_registration_tabs-tab-sample_details").click()
        return fieldValidation.call(this, msg, "sample_type")
    }
    else if (this.state.colour_pieces.length === 0) {
        document.getElementById("sample_registration_tabs-tab-colours_pieces").click()
        alert("Please add colour & pieces details")
    }
    else if (this.state.testParameterAdded.length === 0) {
        document.getElementById("sample_registration_tabs-tab-test_details").click()
        alert("Please add test parameters")
    }
    else if (this.state.sample_image === "") {
        document.getElementById("sample_registration_tabs-tab-documents").click()
        alert("Please attached sample image")
    }
    else {
        this.setState({
            loading: true,
        });
        const requestOptions = {
            method: "POST",
            headers: postHeader(),
            body: JSON.stringify(this.state),
        };
        try {
            const response = await fetch(`${url}/CreateUpdateAPI/`, requestOptions);
            if (response.ok) {
                const data = await response.json();
                if (data) {
                    this.setState({
                        obj_id: data.obj_id
                    })
                    fileUploadFun.call(this, event, `${url}`)
                }
                else if (data.error_msg) {
                    this.setState({
                        err_msg_visible: true,
                        error_message: data.error_msg,
                        displayDate: false,
                        loading: false,
                    });
                    setTimeout(() => {
                        this.setState({
                            err_msg_visible: false,
                        });
                    }, process.env.REACT_APP_ERR_MSG_DUR);
                }
            }
            else {
                console.log(response)
            }
        } catch (e) {
            let error = e.message;
            apiError.call(this, error)
        }

    }
}

export default handleSubmitFun;
