import apiError from "../../../components/apiError";
import postHeader from "../../../components/postHeader";

async function downloadFileFun(e, url) {
    this.setState({
        file_name: (this.state.file_name = e.target.text),
        obj_id: this.state.obj_id
    });
    const requestOptions = {
        method: "POST",
        headers: postHeader(),
        body: JSON.stringify(this.state),
    };
    try {
        const response = await fetch(url, requestOptions);
        if (response.ok) {
            const blob = await response.blob();
            if (blob.size > 0) {
                let url = window.URL.createObjectURL(blob);
                let a = document.createElement("a");
                a.href = url;
                a.download = this.state.file_name;
                a.click();
                this.setState({
                    loading: false,
                });
            } else {
                this.setState({
                    loading: (this.state.loading = false),
                });
                alert("Something went wrong, kindly check with administrator");
            }
        }
    } catch (e) {
        let error = e.message;
        apiError.call(this, error);
    }
}

export default downloadFileFun;
