function drpHandleChangeFun(e){
      this.setState({ [e.target.name]: e.target.value});
        if (e.target.value === "courier") {
            this.setState({
                courierFieldDisplay: true,
                awbFieldDisplay: true,
                handFieldDisplay: false,
            })
        }
        else if (e.target.value === "hand") {
            this.setState({
                courierFieldDisplay: false,
                handFieldDisplay: true,
                awbFieldDisplay: false,

            })
        }
        else if (e.target.value === "hand") {
            this.setState({
                courierFieldDisplay: false,
                awbFieldDisplay: false,
                handFieldDisplay: true,
            })
        }
        else if (e.target.value === "post") {
            this.setState({
                courierFieldDisplay: false,
                awbFieldDisplay: true,
                handFieldDisplay: false,
            })
        }
}

export default drpHandleChangeFun;