async function addInwardNoFun(e, url,number) {
    e.preventDefault();
    const response = await fetch(url);
    if (!response.ok) {
        return alert("Please enter correct Inward No.")
    }
    else {
        const result = await response.json();
        if (result.count > 0) {
            let check = this.state.inwardList.includes(number)
            if (check === true) {
                return alert("Inward number already added!")
            }
            else {
                let ele = document.getElementById("inward_list")
                ele.options[ele.options.length] = new Option(number, number);
                this.setState(prevState => ({
                    inwardList: [...prevState.inwardList, number]
                }))
            }
        }
        else {
            return alert("Please enter correct Inward No.")
        }
    }
}

export default addInwardNoFun;
