async function fileUploadFun(e, url) {
    e.preventDefault();
    this.setState({
        loading: true,
    });
    let csrftoken = ""
    await fetch(`/${process.env.REACT_APP_NAME}/get_csrf/`)
        .then(async (res) => res.json())
        .then((details) => {
            csrftoken = details.token
        });
    var formData = new FormData();
    formData.append('sample_image', this.state.sample_image);
    formData.append('other_doc', this.state.other_doc);
    formData.append('obj_id', this.state.obj_id);
    try {
        await fetch(`${url}/fileUploadAPI/`, {
            method: 'POST',
            body: formData,
            headers: {
                "contentType": 'multipart/form-data',
                "X-CSRFToken": csrftoken
            }
        })
            .then(async (res) => res.json())
            .then((data) => {
                if (data.status) {
                    if (data.status === "Ok") {
                        this.setState({
                            redirect: true,
                            loading: false
                        })
                    }
                    else {
                        this.setState({
                            redirect: true,
                        });
                    }

                } else if (data.error_msg) {
                    this.setState({
                        err_msg_visible: true,
                        error_message: data.error_msg,
                        loading: false,
                    });
                    setTimeout(() => {
                        this.setState({
                            err_msg_visible: false,
                        });
                    }, process.env.REACT_APP_ERR_MSG_DUR);
                }
            })
    }
    catch (e) {
        let error = e.message;
        this.setState({
            err_msg_visible: true,
            error_message: error,
            loading: false,
            logData: []
        });
        setTimeout(() => {
            this.setState({
                err_msg_visible: false,
            });
        }, process.env.REACT_APP_ERR_MSG_DUR);
        // apiError.call(this, error)
    }
}

export default fileUploadFun;
