async function getTestMastersFun() {
    const response = await fetch(`/${process.env.REACT_APP_NAME}/testMaster/ListAPI/?page_size=${process.env.REACT_APP_PAGE_SIZE_FOR_MASTER}&order_field=test_name&order_by=asc`);
    if (!response.ok) {
        return alert("Please enter correct Inward No.")
    }
    else {
        const result = await response.json();
        const TestMasterList = []
        if (result.results.length > 0) {
            result.results.map(i =>
                i.status === true ?
                    TestMasterList.push({ key: i.id, value: i.id, text: i.test_name, test_method: i.test_method, department: i.department })
                    :
                    ''
            )
            this.setState({
                testMasterList: this.state.testMasterList = TestMasterList
            })
            var dv = this.state.test_parameter;
            var res = this.state.testMasterList.filter(
                ({ text }) =>
                    dv === text);
            if (res.length > 0) {
                this.setState({ testParameterSelected: this.state.testParameterSelected = res[0].value })
            }
        }
    }
}

export default getTestMastersFun;
