async function saveColourPiecesFun(e) {
    e.preventDefault();
    if (this.state.currentRcdID === "") {
        let sampleQty = parseInt(this.state.sample_qty)
        let addedQty = parseInt(this.state.added_sample_colour_qty)
        let colourQty = parseInt(this.state.sample_colour_qty)
        var balanceQty = sampleQty - addedQty - colourQty
        var actualBalance = sampleQty - addedQty
        if (this.state.sample_colour === "") {
            document.getElementById("sample_colour").style.bordercolour = "red"
            alert("Please update Sample colour!")
        }
        else if (this.state.sample_colour_qty === "") {
            document.getElementById("sample_colour_qty").style.bordercolour = "red"
            alert("Please update sample colour qty!")
        }
        else if (balanceQty < 0) {
            document.getElementById("sample_colour_qty").style.bordercolour = "red"
            alert(`Please sample colour quantity should not exceed balance quality i.e ${actualBalance}`)
        }
        else {
            let nextNo = parseInt(this.state.colourRowNo) + 1
            let data = {
                "id": nextNo,
                "colour": this.state.sample_colour,
                "quantity": this.state.sample_colour_qty,
                "description_1": this.state.colour_description_1,
                "description_2": this.state.colour_description_2,
                "description_3": this.state.colour_description_3,
            }
            this.setState(prevState => ({
                colour_pieces: [...prevState.colour_pieces, data],
                sample_colour: "",
                sample_colour_qty: '',
                colour_description_1: '',
                colour_description_2: '',
                colour_description_3: '',
                added_sample_colour_qty: sampleQty - balanceQty,
                balanceQty: balanceQty,
                colourRowNo: nextNo,
                show: false,
            }))
        }
    }
    else {
        let balanceQty = this.state.balanceQty
        var prevQty = 0
        let rcdID = parseInt(this.state.currentRcdID)
        for (const [k, v] of Object.entries(this.state.colour_pieces)) {
            if (v.id === rcdID) {
                prevQty = parseInt(v.qty)
                this.state.colour_pieces.splice(parseInt(k), 1);
            }
            this.setState({
                colour_pieces: this.state.colour_pieces
            })
        }
        balanceQty = balanceQty - (parseInt(this.state.sample_colour_qty) - prevQty)
        let data = {
            "id": rcdID,
            "colour": this.state.sample_colour,
            "qty": this.state.sample_colour_qty,
            "description_1": this.state.colour_description_1,
            "description_2": this.state.colour_description_2,
            "description_3": this.state.colour_description_3,
        }
        var added_sample_colour_qty = parseInt(this.state.sample_qty) - balanceQty
        this.setState(prevState => ({
            colour_pieces: [...prevState.colour_pieces, data],
            sample_colour: "",
            sample_colour_qty: '',
            colour_description_1: '',
            colour_description_2: '',
            colour_description_3: '',
            added_sample_colour_qty: added_sample_colour_qty,
            balanceQty: balanceQty,
            show: false,
        }))
    }
}

export default saveColourPiecesFun;