async function getSampleMastersFun() {
    const response = await fetch(`/${process.env.REACT_APP_NAME}/sampleMaster/ListAPI/?page_size=${process.env.REACT_APP_PAGE_SIZE_FOR_MASTER}&order_field=sample_description&order_by=asc`);
    if (!response.ok) {
        return alert("Please enter correct Inward No.")
    }
    else {
        const result = await response.json();
        console.log(result)
        this.setState({
            sample_master_list: result.results
        })
    }
}

export default getSampleMastersFun;
