import moment from "moment";
import getDetailsFun from "../../../components/getDetailsFun";

async function getDetailsFunction(form_name) {
    if (this.props.navOrgName === "" && this.props.adminAccess === false) {
        return alert("Please change organisation to view details");
    }
    this.setState({ loading: true });
    const queryParams = window.location.pathname;
    await getDetailsFun.call(this, queryParams, form_name);
    if (this.state.details !== "") {
        for (const [key, value] of Object.entries(this.state.details)) {
            if (key.includes("date")) {
                let date = moment(value).format('DD-MMM-YYYY')
                this.setState({ [key]: date })
            }
            else {
                this.setState({ [key]: value })
            }
        }
        this.setState({
            obj_id: this.state.details.id,
            testParameterAdded: this.state.details.test_parameters,
            loading: false,
        })
        for (const [key, value] of Object.entries(this.state.details.inward)) {
            let inwardNum = value["inward_no"]
            let ele = document.getElementById("inward_list")
            ele.options[ele.options.length] = new Option(inwardNum, inwardNum);
            this.setState(prevState => ({
                inwardList: [...prevState.inwardList, inwardNum]
            }))
        }
        const scp = await fetch(`/${process.env.REACT_APP_NAME}/sampleRegistration/getSampleColourPiecesListAPI/?sampleRegisterID=${this.state.details.id}`)
        if (scp.ok) {
            const jsonData = await scp.json();
            this.setState(prevState => ({
                colour_pieces: jsonData,
            }))
        }
    }
}

export default getDetailsFunction;
