async function addColourLineFun() {
    if (this.state.colourRowNo === 0) {
        this.setState({
            balanceQty: this.state.sample_qty
        })
    }
    if (this.state.sample_qty === "" || this.state.sample_qty === 0) {
        this.setState({
            colourModalForm: false
        })
    }
    else {
        this.setState({
            colourModalForm: true
        })
    }
    this.setState({
        currentRcdID: "",
        modalTitle: "Add/Edit colour & Pieces",
        modalOpen: "#staticBackdrop",
        show: true,
    })
}

export default addColourLineFun;
